import React from 'react';

class BoxSample extends React.Component{


    render(){
        return (
        <div className="contactBox" ref={this.props.id}>
          <div className="contactHeader">{this.props.mainTitle}</div>
          <div className="contactText"> {this.props.text} </div>
          <p />
        </div>
    )
  }
}

export default BoxSample;